
/** Theme Switcher
 *  Light Mode
 *  Dark  Mode
 */

"use client";

import React, { useState, useEffect } from "react";
import dynamic from "next/dynamic";

const SunIcon = dynamic(() =>
     import("@heroicons/react/24/solid").then(mod => mod.SunIcon),
     {ssr: false});

const MoonIcon = dynamic(() => 
     import("@heroicons/react/24/solid").then(mod => mod.MoonIcon),
     {ssr: false});



const ThemeSwitcher = () => {
    
    /* get local theme */
    const get_initial_theme = () => {
        if (typeof window !== "undefined") {
            const saved_theme = localStorage.getItem("theme");
            return saved_theme ? saved_theme : "light";
        }
        return "light";
    };

    const [theme, set_theme] = useState(get_initial_theme);

    const switch_theme = () => {
        /** debug
         *  console.log("Theme switched!");
         */
        const new_theme = theme === "light" ? "dark" : "light";
        set_theme(new_theme);
        document.body.setAttribute("data_theme", new_theme);
        localStorage.setItem("theme", new_theme);        
    }

    useEffect(() => {
        document.body.setAttribute("data-theme", theme);
    }, [theme]);

    return (
        <button onClick={switch_theme}
                className="px-4
                           py-2
                           bg-blue-500 
                           text-white 
                           rounded-full 
                           hover:bg-blue-600
                           transition
                           duration-200
                           w-full
                           "
        >{ theme =="light" ? (
            <SunIcon className="h-5 w-5"/>
        ) : (
            <MoonIcon className="h-5 w-5"/>
        )}
        </button>      
    );
};

export default ThemeSwitcher;